.newsslider ::-webkit-scrollbar {
	height: 3px;
	margin-bottom: 0;
	background-size: 0 100%;
	background-repeat: no-repeat;
	overflow: hidden;
	transition: width .4s ease-in-out;
	width: 0;
	cursor: pointer;
	transition: all 0.3s ease;
}


/* Track */

.newsslider ::-webkit-scrollbar-track {
	cursor: pointer;
	height: 9px;
	border-bottom: 1px solid rgba(10, 0, 51, 0.1);
}


/* Handle */

.newsslider ::-webkit-scrollbar-thumb {
	background: #0A0033;
	border-radius: 5px;
	margin-bottom: -1;
	display: block;
	height: 3px;
	cursor: pointer;
	margin-left: 10px;
	transition: all 0.3s ease;
}


/* Handle on hover */

.newsslider:hover ::-webkit-scrollbar-thumb {
	background: #4563FF;
	cursor: pointer;
	background: #4563FF;
	border-radius: 5px;
}

.newsslider:hover ::-webkit-scrollbar {
	cursor: pointer;
	border-radius: 5px;
}

.newsslider:hover .newsslider__block {}


