.partnersslider ::-webkit-scrollbar {
	height: 3px;
	margin-bottom: 0;
	background-size: 0 100%;
	background-repeat: no-repeat;
	overflow: hidden;
	transition: width .4s ease-in-out;
	width: 0;
	cursor: pointer;
}


